.popup__title {
  max-width: 330px;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  margin: 0 0 54px;
}

@media screen and (max-width: 649px) and (min-width: 320px) {
  .popup__title {
    max-width: 238px;
    font-size: 18px;
    line-height: 22px;
    margin-top: 10px;
  }
}
