.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  cursor: pointer;
  transition: all 0.3s linear;
}
